import axios from 'axios'
import extractIssueData from '../utils/extract-issue-data'

class GitHub {
  constructor(token) {
    this.client = axios.create({
      baseURL: 'https://api.github.com',
      headers: {
        Authorization: `token ${token}`,
      },
    })
  }
  async verify() {
    let user = await this.client.get('/user')
    return user
  }
  async getRepos() {
    let repos = await this.client.get(`/user/repos`)
    return repos
  }
  async postIssue(user, repo, text) {
    let data = extractIssueData(text)
    let res = await this.client.post(`/repos/${user}/${repo}/issues`, data)
    return res
  }
  async getIssues(user, repo, labels = '',page=1) {
    const res = await this.client.get(
      `/repos/${user}/${repo}/issues?per_page=15&page=${page}&labels=${labels}`
    )
    return res
  }
  async getTags(user, repo) {
    const res = await this.client.get(`/repos/${user}/${repo}/labels`)
    return res
  }
}

export default GitHub
