function extractIssueData(text) {
  const data = {}
  const tRegexp = /# (?<title>[a-zA-Z0-9_\u4E00-\u9FFF/-]+)\n/
  if (tRegexp.test(text)) {
    let titleResult = text.match(tRegexp)
    data.title = titleResult.groups.title
    data.body = text.replace(titleResult, '')
  } else {
    data.title = text
  }
  const lRegexp = /#(?<label>[a-zA-Z0-9_\u4E00-\u9FFF/-]+)/g
  let labelResult = text.matchAll(lRegexp)
  data.labels = [...labelResult].map((item) => {
      return item.groups.label
  })
  return data
}

export default extractIssueData
