import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Repo from '../views/Repo.vue'
import Home from '../views/Home.vue'
import Search from '../views/Search.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home,
  },
  {
    path: '/search',
    component: Search,
  },
  {
    path: '/login',
    component: Login,
    meta: { isPublic: true }
  },
  {
    path: '/repo',
    component: Repo,
    meta: { isPublic: true }
  },
]

const router = new VueRouter({
  mode:"hash",
  routes,
})

router.beforeEach((to, from, next) => {
  if (!to.meta.isPublic && !(localStorage.token &&localStorage.user &&localStorage.repo)) {
    return next('/login')
  }
  next()
})
export default router
