<template>
  <div id="search">
    <NavBar />
    <div class="content-container pa-10" id="issues">
      <div class="search">
        <v-text-field
          color="primary"
          :loading="fetching"
          v-model="tags"
          label="ËØ∑ËæìÂÖ• tag,‰ª•ÈÄóÂè∑ÂàÜÈöî"
        ></v-text-field>
        <div class="btn-wrap">
          <v-btn icon @click="fetchIssues()"
            ><v-icon color="success">mdi-cloud-search</v-icon></v-btn
          >
        </div>
        <div class="issue">
          <v-card
            class="ms-auto status mb-4 ml-5 mr-5"
            v-for="(issue, index) in issues"
            :index="`issue-item-${index}`"
            :key="`issue-item-${index}`"
          >
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>{{ issue.title }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <div class="d-flex flex-no-wrap justify-space-between">
              <div>
                <v-card-text
                  class="issue-body text--primary"
                  v-html="issue.body"
                ></v-card-text>
              </div>
            </div>
            <v-card-actions>
              <span class="fromNow card-footer">{{
                fromNow(issue.created_at)
              }}</span>
              <span class="tags card-footer"
                ><span
                  class="tags"
                  v-for="(tag, index) in issue.labels"
                  :index="`issue-item-${index}`"
                  :key="`issue-item-${index}`"
                >
                  <a
                    :href="
                      tag.url.replace('api.github.com/repos', 'github.com')
                    "
                    >#{{ tag.name }}#</a
                  >
                </span>
              </span>
              <v-spacer></v-spacer>
              <v-btn icon>
                <a :href="issue.html_url" style="text-decoration: none"
                  ><v-icon>mdi-github</v-icon></a
                >
              </v-btn>
            </v-card-actions>
          </v-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Github from "../api/github";
import NavBar from "../components/NavBar.vue";
import moment from "moment";
moment.locale("zh-cn");
export default {
  components: {
    NavBar,
  },
  data() {
    return {
      tags: "",
      fetching: false,
      issues: [],
      page: 1,
      isLoading: false,
    };
  },
  methods: {
    async login() {
      this.$currentUser = new Github(localStorage.token);
    },
    async fetchIssues() {
      this.page=1
      this.tags = this.tags.replace("Ôºå", ",");
      this.fetching = true;

      let res = await this.$currentUser.getIssues(
        localStorage.user,
        localStorage.repo,
        this.tags
      );
      if (res.status == "200") {
        this.fetching = false;
        this.issues = res.data;
      }
    },
    fromNow(time) {
      return moment(time).fromNow();
    },
    async scroll() {
      let isLoading = false;
      window.onscroll = async () => {
        // Ë∑ùÁ¶ªÂ∫ïÈÉ®200pxÊó∂Âä†ËΩΩ‰∏ÄÊ¨°
        let bottomOfWindow =
          document.documentElement.offsetHeight -
            document.documentElement.scrollTop -
            window.innerHeight <=
          20;
        if (bottomOfWindow && isLoading == false) {
          isLoading = true;
          let res = await this.$currentUser.getIssues(
            localStorage.user,
            localStorage.repo,
            this.tags,
            this.page + 1
          );
          if (res.status == "200") {
            if (res.data.length > 1) {
              this.issues = this.issues.concat(res.data);
              this.page += 1;
              isLoading = false;
            }
          }
        }
      };
    },
  },
  async created() {
    this.login();
  },
  async beforeUpdate() {
    this.scroll();
  },
};
</script>

<style scoped>
.content-container {
  margin-top: 48px !important;
}
.btn-wrap {
  display: flex;
  justify-content: flex-end;
}
.card-footer {
  font-size: 12px;
}
.fromNow {
  color: #888888;
}
.issue-body {
  white-space: pre-line;
}
</style>
