    </div>
    <div class="dialog">
      <v-dialog v-model="dialog" persistent max-width="290">
        <v-card>
          <v-card-title class="text-h6"> ü§î ‰Ω†Á°ÆÂÆöÁî®{{this.selectedRepo}}Ëøô‰∏™‰ªìÂ∫ìÊî∂ÈõÜ‰Ω†ÁöÑÊÉ≥Ê≥ïÂêó?</v-card-title>
          <v-card-text>ÁÇπÂáªÁ°ÆÂÆöÔºåÈÖçÁΩÆÂÆåÊàêÔºÅÂêéÁª≠ÈÖçÁΩÆÈ°µÈù¢‰∏çÂÜçÊòæÁ§∫Ôºå‰∏ìÊ≥®ËÆ∞ÂΩïÊÉ≥Ê≥ïÔºöÔºâ</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="dialog = false">
              ÂÜçÊÉ≥ÊÉ≥
            </v-btn>
