<script>
import GitHub from "../api/github";
export default {
  data() {
    return {
      repos: [],
      selectedRepo: "",
      dialog: false,
    };
  },
  computed: {},
  methods: {
    visibilityIcon(private_repo) {
      return private_repo ? "mdi-eye-off" : "mdi-eye";
    },
    async fetchRepos() {
      let user = new GitHub(localStorage.token);
      const res = await user.getRepos();
      this.repos = res.data;
    },
    selectRepo(name) {
      this.selectedRepo = name;
      this.dialog=true
    },
    saveRepo(){
      localStorage.repo=this.selectedRepo
      this.dialog=false
      this.$message.success("ÈÖçÁΩÆÂÆåÊØïÔºåÂç≥Â∞ÜË∑≥ËΩ¨Ëá≥ÂÜô‰ΩúÈ°µ‚Ä¶‚Ä¶")
      this.$router.push("/")
    }
  },
  async created() {
    this.fetchRepos();
  },
};
</script>
