<template>
  <div id="auth">
    <v-form ref="form" class="ma-5">
      <v-text-field
        v-model="token"
        label="token"
        color="success"
      ></v-text-field>
      <div class="btn-wrap">
        <v-btn color="success" class="mr-2" @click="login()">
          <v-icon class="mr-2">mdi-github</v-icon>
            ÁôªÂΩï
          </v-btn>
      </div>
    </v-form>
  </div>
</template>

<script>
import GitHub from '../api/github'
export default {
  name: "Login",
  data(){
    return {
     token:""
    }
  },
  methods:{
    async login(){
      if(this.token==""){
        this.$message.error("token ‰∏çËÉΩ‰∏∫Á©∫")
      }else{
        let github = new GitHub(this.token)
        let res = await github.verify()
        let user = res.data
        if(user.login){
          this.$message.success("ÁôªÂΩïÊàêÂäü")
          localStorage.token = this.token
          localStorage.user = user.login
          this.$router.push('/repo')
        }else{
          this.$message.error("token ÊúâËØØ")
        }
      }

    }
  }
};
</script>
<style scoped>
.btn-wrap{
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
